import React from 'react';
import { LocalizationProvider as MuiLocalizationProvider, LocalizationProviderProps } from '@mui/x-date-pickers/LocalizationProvider';
import { AdapterDayjs } from '@mui/x-date-pickers/AdapterDayjs';

// TODO: fix the any type, only when passing all props there is a problem with the type, when using only props.children it works fine.
// The need for passing the props is to allow overriding the default dateAdapter, when wanting to use a different dates library.
const LocalizationProvider = <TDate, TLocale>( props: LocalizationProviderProps<TDate, TLocale> ) => (
	// eslint-disable-next-line @typescript-eslint/no-explicit-any
	<MuiLocalizationProvider dateAdapter={ AdapterDayjs as any } { ...props } />
);

export default LocalizationProvider;
